.App {
  margin-top: 15px;
  font-family: 'Open Sans' sans-serif;
}

.App .navbar {
	color: #ccc;
}

.App .navbar-brand {
  font-weight: bold;
}

.App .navbar-default {
	background-color: #abcdef
}

.img-responsive {
  padding: 10%;
}

.img-title:hover {
	-webkit-transform: scale(1, 1) !important;
	    -ms-transform: scale(1, 1) !important;
	        transform: scale(1, 1) !important;
}
.carousel-container {
	max-height: 50vh;
}

.gallery-img {
	max-height: 20%;
	margin-left: auto;
	margin-right: auto;
}




